import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout"
import CourseLayout from "../components/courseLayout"

import { Table } from "react-bootstrap" 


const CourseRating = ({ pageContext: { course } }) => {
		return (

		  <Layout>
		  	<CourseLayout>
		    	<h2>Rating</h2>
		    	<p  className="pHomework">{course.breed}</p>

		    	<Table striped bordered hover size="sm">
				  <thead>
				    <tr>
				      <th>#</th>
				      <th>Student</th>
				      <th>Rating</th>
				      <th>Final Grade</th>
				    </tr>
				  </thead>
				  <tbody>
				  {course.rating.map(rating =>
				    <tr>
				      <td>{rating.id}</td>
				      <td>{rating.firstname}</td>
				      <td>{rating.rating}</td>
				      <td>?</td>
				    </tr>
				    )}
				  </tbody>
				</Table>

		    </CourseLayout>
		  </Layout>

	)
}

export default CourseRating
